.contact-section{
  height: 100vh;
  width: 100%;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
}

.contact-information {
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}


.contact-options {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 80vw;
  
    p {
      color: white;
      font-family: 'Ubuntu Mono', monospace;
      text-shadow: rgb(126, 126, 126) 0px 0px 5px;
      font-size: larger;
      font-weight: bold;
    }
  
    img {
      height: 110px;
    }
  
}
.contact-option {
    margin: 10px;
    margin-right: 30px;
    font-size: 65px;
    color: white;
}
